import "./styles/card.css";

// import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import LocalPhoneIcon from "@mui/icons-material/LocalPhone";
import RoomOutlinedIcon from "@mui/icons-material/RoomOutlined";
import WhatsAppIcon from "@mui/icons-material/WhatsApp";
import MailOutlineIcon from "@mui/icons-material/MailOutline";
export default function Card(props) {
  return (
    <>
      <div className="card-container" loading="lazy">
        <img
          className="card-img"
          src={require(`./img/people${props.img}.jpg`)}
          alt="Employee"
        ></img>
        <div className="card-people">
          <h1 className="card-title">{props.name}</h1>
          <p className="card-occupation">{props.position}</p>

          <p className="card-section">{props.section}</p>
          <main className="card-info">
            <p className="card-email">
              <MailOutlineIcon className="icon" />
              {props.email}
            </p>

            <p className="card-phone">
              {" "}
              <LocalPhoneIcon className="icon" /> {props.phone}
            </p>
            <p className="card-whatsapp">
              <WhatsAppIcon className="icon" />
              {props.whatsapp}
            </p>
            <p className="card-direction">
              {" "}
              <RoomOutlinedIcon className="icon" /> {props.direct}
            </p>
          </main>
        </div>
      </div>
    </>
  );
}
